(function () {
    'use strict';
    var angular = require('angular');
    //require('./signzyModal.modal.component.scss')

    var modalController = function (analyticService) {
        var vm = this;

        vm.$onInit = function () {
          //  analyticService.buttonList.popup.event_content = "Assignment of Esignzy adhar e signing";
            //analyticService.trackUserAction(analyticService.buttonList.popup);
        };

        /**
         * This function is triggered when we select or unselect card.
         * We push the selected card in selectedPrimaryCards array and
         *  pop the unselected card.
         */
        vm.Close = function (isSelected) {
            vm.close({
                $value: isSelected
            });
        };

        vm.cancel = function () {
            vm.dismiss({
                $value: 'cancel'
            });
        };
    };

    var modalComp = angular.module('journey.module').component('signzyModal', {
        template: require('./signzyModal.modal.component.html'),
        bindings: {
            resolve: '<',
            close: '&',
            dismiss: '&'
        },
        controller: ['analyticService', modalController]
    });
    module.exports = modalComp.name;
})();



