module.exports = "<div ng-hide=$ctrl.showLoader class=row> <div class=\"col-md-12 col-sm-12 col-xs-12\" ui-view=step8View> <form ng-hide=$ctrl.showLoader id=step8 name=step8Form focus-on-error novalidate> <input type=text class=hidden-field ng-model=$ctrl.robocheck tabindex=-1> <div class=row> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <h1 class=\"brand-bar no-margin-top\">Agreement to Terms & Conditions</h1> </div> </div> <alert-msg ng-if=$ctrl.errMsg type=ERROR> {{$ctrl.errMsg}} </alert-msg> <alert-msg ng-if=$ctrl.successMsg type=SUCCESS close=$ctrl.closeSuccessMsg()> {{$ctrl.successMsg}} </alert-msg> <div class=row ng-if=$ctrl.isStaffJourney> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <div> Steps to complete E-Sign : </div> <div> 1. By clicking on \"Proceed with E-sign\", you will get the invitation link for e-sign in email and SMS. </div> <div> 2. You need to open the link to initiate the process. </div> <div> 3. Preview the document, enter the required details, and click on \"Proceed\". </div> <div> 4. Give your consent by clicking the check box and continue with \"Sign Document\". </div> <div> 5. Enter your Aadhaar number and submit the OTP received on mobile number registered with Aadhaar. </div> <div> 6. Once the OTP is verified you can download the signed document after clicking ok. You will also get a copy of signed document in email. </div> <div> 7. Go to your HSBC Credit card Application to further complete the process. </div> </div> </div> <div class=row ng-if=!$ctrl.isStaffJourney> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <uib-accordion close-others=oneAtATime> <div uib-accordion-group is-open=$ctrl.status[0].open ng-init=\"\" class=panel-default> <uib-accordion-heading> <span class=accordianHeader>applicants declaration</span> <span style=float:right> <span ng-if=$ctrl.status[0].open class=\"icon icon-chevron-up\"></span> <span ng-if=!$ctrl.status[0].open class=\"icon icon-chevron-down\"></span> </span> </uib-accordion-heading> <div> <p class=para>I hereby declare that the above information is complete and true and the Bank is entitled to verify this directly or through any third party agent. I further state that the Bank may at its sole discretion sanction or decline this application for primary and/or add-on applicant (s). I confirm that the add-on applicant(s) and I will jointly and severally be bound by the terms and conditions of usage of the credit cards, copy of which are available on the HSBC website and would be made available on approval of the card. </p> <p class=para>I authorise HSBC to debit my credit card account for any fees, charges, interest, etc. I irrevocably confirm that all dues in respect of the HSBC Credit Card utilised for services availed of and goods purchased from eligible service establishments/merchants, by me and any add-on cardholder(s), will be payable by me and I guarantee the payment towards the same in my capacity as the principal cardholder. I undertake and declare that usage of the credit card by me and any add-on applicant(s) will be strictly in accordance with the Foreign Exchange Management Act, 1999 (\"FEMA\") and the applicable rules, regulations, notifications, directions or orders made thereunder and I confirm that I have (on behalf of HSBC obtained undertaking(s) from the add-on applicant(s) to this effect. In the event of failure by me or any add-on applicant(s) to comply with the provisions of FEMA, as aforesaid, I and such add-on applicant(s) will be liable for action under FEMA. I authorise HSBC to, without notice, combine or consolidate the outstanding balance on the card account with any other account(s) which I maintain with HSBC, and set off or transfer any money outstanding to the credit of such other account(s), in or towards the satisfaction of my liability to HSBC under this Agreement. I/We acknowledge that the Bank may assign any activities to any service provider, whether located in India or overseas and whether an HSBC Group entity or a third party, at its sole discretion, in accordance with the applicable guidelines of the Reserve Bank of India (RBI). I/We also acknowledge the right of the Bank to provide details of my/our account/application to such service providers for the purpose of availing outsourced services of any nature by the Bank, without any specific consent I acknowledge that the existence of this account and details thereof (including details of credit card transactions and any defaults committed by me), will be recorded with credit reference agencies and such information (including processed information) may be shared with banks/financial institutions and other credit grantors for the purposes of assessing further applications for credit by you and/or members of your household, and for occasional debt tracing and fraud prevention.</p> <p class=para>I accordingly authorise the Bank to share information relating to my credit card account/ application, including information relating to any add-on applicant(s). I understand that as a precondition, relating to grant of loans/advances/other non-fund-based credit facilities to me, HSBC requires consent for the disclosure by HSBC, of information and data relating to me/ add-on applicant(s), of the credit facility availed of / to be availed of by me, obligations assumed /to be assumed by me, in relation thereto and default, if any, committed by me in discharge thereof.</p> <ol class=para-margin> <li> Accordingly, I hereby agree and give consent and confirm that I have obtained consent from the add-on applicant(s) for the disclosure by HSBC of all or any such; (a) information and data relating to me/add-on applicant(s) (b) the information or data relating to any credit facility availed of/to be availed of by me and (c) default, if any, committed by me in discharge of my such obligation, as HSBC may deem appropriate and necessary, to disclose and furnish to Credit Information Bureau (India) Ltd. and any other agency authorised in this behalf by RBI. </li> <li> I undertake on my behalf and on behalf of the add-on applicant (s) that (a) the Credit Information Bureau (India) Ltd. and any other agency so authorised may use, process the said information and data disclosed by HSBC; and (b) the Credit Information Bureau (India) Ltd. and any other agency so authorised may furnish for consideration, the processed information and data or products thereof prepared by them, to banks/financial institutions and other credit grantors, as may be specified by the Reserve Bank in this behalf. </li> <li> I expressly authorise HSBC to decrease the credit limit on my credit card, based on its credit assessment, without prior notice to me. </li> <li> I hereby declare that I am not a near relation to any senior officer of the Bank or any directors of any HSBC Group Companies (please strike off if this is not the case and provide a separate list of relevant related persons). </li> <li> I confirm that as on date of this application, there are no litigations, initiated by other banks/Financial Institutions, being faced by me (please strike off if this is not the case and provide a separate list of initiated/pending litigations) in addition to any general right to lien/set-off or other right conferred by law or under any other agreement. </li> </ol> <p class=para>I hereby declare that all the decision of HSBC, in respect to the issuance of the Credit Card to me/the Add-on applicant(s) shall be binding on me and neither me nor any person claiming through me, shall raise any dispute in this regard.</p> <p class=para> I agree that credit limits are subject to periodic review and will vary based on the bank's credit assessment of my card account. Accordingly, I expressly grant consent and authorise HSBC to decrease my credit limit, if required.</p> <p class=para> I hereby declare that I have read and understood the Most Important Terms and Conditions as mentioned in the Application Form. </p> <p class=para> I undertake to inform the bank regarding any change in my residence / employment address and to provide any further information that the bank may require to process my application. </p> <p class=para> I understand that the documents and the application form submitted will be treated as a property of HSBC and will not be returned. </p> <p class=\"para last-para\">I/We consent and authorise HSBC to generate subsequently an additional report through Credit Information Companies across this document or authorised agency/bureau to process my Credit Card application, in the event the necessary documentation and processes are not completed as per HSBC's policies. In the event of change in address due to relocation or any other reason, I shall intimate the new address to HSBC within two weeks of such a change. </p> </div> </div> </uib-accordion> </div> </div> <div class=row ng-if=!$ctrl.isStaffJourney> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <uib-accordion close-others=oneAtATime> <div uib-accordion-group is-open=$ctrl.status[1].open ng-init=\"\" class=panel-default> <uib-accordion-heading> <span class=accordianHeader>disclosures</span> <span style=float:right> <span ng-if=$ctrl.status[1].open class=\"icon icon-chevron-up\"></span> <span ng-if=!$ctrl.status[1].open class=\"icon icon-chevron-down\"></span> </span> </uib-accordion-heading> <div> <p class=para>The applicant acknowledges the right of the Bank to provide details of his/her credit card account to third party agencies for the purpose of availing of support services of any nature by the Bank, without any specific consent or authorisation from him/her</p> <ul class=\"disk para-margin\"> <li> The applicant further acknowledges that the Bank is authorised to share information relating to the applicant, including information relating to any default committed by the applicant in discharge of his/her obligation, as HSBC India may deem appropriate and necessary, with any existing or future credit bureaus/credit reference agencies as determined by the Bank from time to time. </li> </ul> <p class=para> Accordingly, the applicant herein expressly gives consent to disclose information to such credit bureaus/credit reference agencies. Such entities may further make available processed information or data or products thereof to banks/financial institutions and other credit grantors </p> <ol class=\"disk para-margin\"> <li> Bank also provides information relating to credit history/repayment record of the applicant to a credit information company (specifically authorised by the RBI), in terms of the Credit Information Companies (Regulation) Act, 2005 </li> <li> For credit card accounts where correspondence from the Bank is currently withheld due to non delivery of earlier correspondence, then the Bank will make attempts to contact the customer to update the correct address details. </li> </ol> <p class=\"para last-para\"> If the Bank is unable to establish contact with the customer, the operation of the account will be restricted by providing a prior notice of 30 days until the correct address is updated on the Bank's records with address proof, or until the customer confirms the current address on the Bank's records to be correct </p> <h5 class=\"para-heading text-uppercase\">Tax Representation</h5> <ul class=disk> <li> Individual accounts: You are responsible for fulfilling any obligation that you may have with respect to the filing of returns or other required documentation in respect of and the payment of all relevant taxes, including, without limitation, all income, capital gains, wealth and estate duties, stamp duties, Service Tax, Value Added Tax and any other applicable taxes. The creation and continued operation of your account and/or the acquisition, holding or disposal of investments or assets in such account, as well as any income, distributions or losses realized in relation to the operation of the account may expose you to tax consequences depending on a number of factors including, but not limited to, your applicable domicile, your place of residence, your citizenship or the type of assets you hold in your account. Certain countries may have tax legislation with extraterritorial effect regardless of your place of domicile, residence or citizenship. The Bank does not provide any legal or tax advice and you should seek legal and/or tax advice from an independent legal and/or tax adviser. You acknowledge and agree that the Bank has no liability in respect of any of your tax obligations and/or any legal and/or tax advice provided to you by third parties </li> </ul> <h5 class=\"para-heading text-uppercase\">Sanction Clause</h5> <ol class=disk> <li> The Bank and other members of the HSBC Group are required to and may take any action to meet Compliance Obligations relating to or in connection with the detection, investigation and prevention of Financial Crime (Financial Crime Risk Management Activity) and act in accordance with the laws, regulations and requests of public and regulatory authorities operating in various jurisdictions which relate to Financial Crime. The Bank may take, and may instruct (or be instructed by) any other member of the HSBC Group to take, any action which it or such other member, in its sole and absolute discretion, considers appropriate to take in accordance with all such laws, regulations and requests </li> <li> Such action may include but is not limited to (a) screening, intercepting and investigating any instruction, communication, drawdown request, application for services, or any payment sent to or by you, or on your behalf, (b) investigating the source of or intended recipient of funds (c) combining Customer information with other related information in the possession of the HSBC Group, and/or (d) making further enquiries as to the status of a person or entity, whether they are subject to a sanctions regime, or confirming your identity and status and (e) share information on a confidential basis with such HSBC Group offices, whether located in India or overseas in relation to prevention of Financial Crime </li> <li> Exceptionally, our Financial Crime Risk Management Activity may lead to us delaying, blocking or refusing the making or clearing of any payment, the processing of your instructions or application for services or the provision of all or part of the services. To the extent permissible by law, neither we nor any other member of HSBC Group shall be liable to you or any third party in respect of any loss (whether direct or consequential and including, without limitation, loss of profit or interest, however it arose) that was suffered or incurred by you or a third party, caused in whole or in part in connection with the undertaking of Financial Crime Risk Management Activity For the purpose of the present clause </li> <li> Compliance Obligations' means obligations of the HSBC Group to comply with: (a) laws or international guidance and internal policies or procedures, (b) any demand or request from authorities or reporting, disclosure or other obligations under laws, and (c) laws requiring us to verify the identity of our customers </li> <li> 'Financial Crime' includes money laundering, terrorist financing, bribery, corruption, tax evasion, fraud, evasion of economic or trade sanctions, and/or any acts or attempts to circumvent or violate any laws relating to these matters </li> </ol> <h5 class=para-heading>CRS and CKYCR Declaration/Consent clause:</h5> <p class=para>I have read and understood the information requirements and the Terms and Conditions mentioned in this Form (and hereby confirm that the information provided by me on this Form is true, correct and complete. I hereby agree and confirm to inform The Hongkong and Shanghai Banking Corporation Limited, India (HSBC India) for any modification to this information promptly. I further agree to abide by the provisions relating to 'Foreign Account Tax Compliance Act (FATCA) and Common Reporting Standards (CRS) on Automatic Exchange of Information (AEOI) provided in Rules 114F to 114H of the Income Tax Rules, 1962'. </p> <p class=para> I authorize you to update your records basis the above information. I also authorize you to share the given information provided by me with the any statutory/regulatory authorities, as appropriate or HSBC group entities. </p> <p class=para> I understand that the details provided in the form will be reconciled with your existing records and any discrepancy thereof will be duly highlighted to me for further rectification/ updation of records. </p> <p class=para> I acknowledge that the information contained in this form and information regarding the accountholder and any Reportable Account(s) may be provided to the tax authorities of the country in which this account(s) is/are maintained and exchanged with tax authorities of another country or countries in which the account holder may be tax resident pursuant to intergovernmental agreements to exchange financial account information. </p> <p class=para> I certify that I am the accountholder (or am authorized to sign for the accountholder) of all the account(s) to which this form relates. </p> <p class=para> I hereby declare that the details furnished above are true and correct to the best of my knowledge and belief and I under take to inform you of any changes therein immediately. </p> <p class=para> In case any of the above information is found to be false or untrue or misleading or misrepresenting, I am aware that I may be held liable for it. </p> <p class=para> I hereby consent for receiving information from Central KYC Registry through SMS/Email on the above mentioned registered number/email address. </p> <p class=para> I certify that where I have provided information regarding any other person (such as a Controlling Person or other Reportable Person to which this form relates) that I will, within 30 days of signing this form, notify those persons that I have provided such information to HSBC and that such information may be provided to the tax authorities of the country in which the account(s) is/are maintained and exchanged with tax authorities of another country or countries in which the person may be tax resident pursuant to intergovernmental agreements to exchange financial account information. </p> <p class=para> I undertake to advise The Hongkong and Shanghai Banking Corporation Limited within 30 days of any change in my circumstances which affects the tax residency status or causes the information contained herein to become incorrect, and to provide HSBC with a suitably updated self-certification and declaration within 90 days of such change in circumstances</p> <h5 class=\"para-heading text-uppercase\">Know Your Customer/Due Diligence Requirement</h5> <ul class=disk> <li> In line with the requirements of the Bank's policy, the Know Your Customer (KYC) documents along with other documentation need to be submitted at the time of availing a new card/account or as and when requested by the Bank. <br> The Bank reserves a right to allow/restrict operations in a newly opened/existing credit card accounts maintained with the Bank, if the customer is not able to satisfy the due diligence requirements in line with the Bank's policy </li> </ul> <h5 class=\"para-heading text-uppercase\">Account Rules and Terms and Conditions</h5> <ul class=\"disk last-list\"> <li> The Bank reserves a right to block transactions/allow operations in a newly opened/existing credit card accounts maintained with the Bank, if the account deliverables/welcome letter/welcome pack/bank correspondences not limited to account statements, etc., are returned undelivered and the Bank is unable to contact the accountholder at the address/contact number provided by them at the time of account opening/updated in bank records </li> </ul> </div> </div> </uib-accordion> </div> </div> <div class=row ng-if=!$ctrl.isStaffJourney> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <uib-accordion close-others=oneAtATime> <div uib-accordion-group is-open=$ctrl.status[2].open ng-init=\"\" class=panel-default> <uib-accordion-heading> <span class=accordianHeader>Credit Card Application Terms and Conditions</span> <span style=float:right> <span ng-if=$ctrl.status[2].open class=\"icon icon-chevron-up\"></span> <span ng-if=!$ctrl.status[2].open class=\"icon icon-chevron-down\"></span> </span> </uib-accordion-heading> <div class=creditCardTc> <h5 class=para-heading><b>Customer Declarations:</b></h5> <h5 class=para-heading><b>Information given on Tablet / Mobile Device</b></h5> <p class=para>Icertify that the information provided by me in this application and collected in the e-application form is true and correct in all respects and HSBC is entitled to verify this directly or through any third party agent. I also agree that if any of the above details or the confirmation made by me/us is found to be false, then HSBC will be entitled to revoke and/or recall the credit facility. </p> <h5 class=para-heading><b>I understand and agree</b></h5> <p class=para>&#9900; to use click wrap as a mode of accepting these T & C's appearing hereunder and as a mode of authenticating the contents of the credit card application submitted by me online; </p> <p class=para>&#9900; and acknowledge that the application form submitted online will be deemed to be a document executed by me and HSBC India will be entitled to treat that as a genuine and binding mode of execution; </p> <p class=para>&#9900; by way of click wrap, I have accorded my written approval/consent to the authorizations, declarations, undertakings and terms and conditions mentioned therein and hereby authorized HSBC India to rely upon the same; </p> <p class=para>&#9900; I shall not raise any dispute in relation to the application form submitted online on the ground that the same was not signed on wet ink by me; </p> <p class=para>&#9900; That the Bank will process this application based on my above representation.. </p> <h5 class=para-heading><b>For cases with PAN Card</b></h5> <p class=para>I authorize HSBC to validate my/our Permanent Account Number (PAN) from the database authorized by Income Tax Department. </p> <h5 class=para-heading><b>For Cases with Aadhaar Card</b></h5> <p class=para>This is to confirm that I/we am/are voluntarily providing a physical copy of my Aadhaar card / e-Aadhaar card in a masked form to HSBC for the purpose of establishing my/our identity / address proof to set up a banking relationship and process instructions in relation to the same. I hereby provide my consent for verification of my Aadhaar to establish its genuineness through such means acceptable as per UIDAI or under any Act or law from time to time. The consent and purpose of collecting Aadhaar have been explained to me and I confirm having understood the same. I acknowledge that the Bank will store the said Aadhaar number in a masked form in their records and use the same for such purposes as may be permitted. I also acknowledge that the Bank has provided me/us with alternate options in terms of identity documents that could be provided in lieu of Aadhaar for setting up a banking relationship </p> <p class=para>I authorize HSBC to upload/refresh/update the demographic and other details in the Central KYC Registry in terms of the provisions of the Prevention of Money Laundering Act and Rules notified thereunder from time to time and create a new or update an existing KYC number that has been provided with the application, as per the operating guidelines issued by the CKYC Registrar and the Reserve Bank of India. </p> <h5 class=para-heading><b>Product Information (applicable for product benefits and features)</b></h5> <p class=para>The HSBC Credit Card sales representative has provided me with the necessary information to make an informed decision and I have independently taken the decision to apply for theCredit Card. I understand the features of the product, its terms, conditions and applicable charges on availing this product. </p> <h5 class=para-heading><b>Please ensure that you have read all the information above before providing your click wrap acceptance to these T& C�s.</b></h5> <p class=para>By signing this form, I am agreeing to the terms and conditions applicable on availing of a Credit Card from HSBC India. I confirm having read and understood the Most Important Terms and Conditions (MITC) including the Tariff Sheet enclosed with this application form. I further agree that any facility that may be provided to me shall be governed by the internal policies of HSBC that may be in force from time to time and I shall be bound by them. </p> <p class=para>I confirm having signed on the Digital Platformand by signing this digital form, I hereby authorize HSBC India to take on record the Credit Card application and process the same. </p> <p class=para>I confirm and declare that I am not a near relation to any senior officer of the Bank or any directors of any HSBC Group Companies* (*Definitions as uploaded on PWS)(if No, please strike off this and provide separate relationship declaration). </p> <p class=para>I hereby authorize HSBC to assign any activities to any service provider, whether located in India or overseas and/or to any HSBC Group entity or a third party, at its sole discretion, in accordance with the applicable guidelines of the Reserve Bank of India (RBI). I also authorize HSBC to share information pertaining to me with its authorized service providers for the purpose of availing outsourced services of any nature by HSBC, without any further specific consent or authorization from me. I further authorize HSBC or such service providers, whether located in India or overseas, to disclose such information if required or permitted by any law, rule or regulation or at the request of any governmental or regulatory authority or if such disclosure is required for the purposes of preventing fraud. </p> <p class=para>I/We hereby confirm having agreed to receive all service communications from HSBC India on an ongoing basis through e-mails sent to the e-mail IDs registered by me/us with HSBC India. The communication may include account related information, certificates, bank notices and account statements. I/We agree to receive the account statement on e-mail pursuant to which and HSBC India will send the Statement in an electronic format to my e-mail ID specified on HSBC India�s records in lieu of physical statements. I/We shall notify HSBC India of any change in my/ our e-mail ID. HSBC India will be deemed to have delivered the statement to me/us upon HSBC India not receiving a delivery failure notification. Should I/We experience any difficulty in accessing the electronically delivered statement, I/we shall promptly advise HSBC India for delivery through alternate means. Failure to advise HSBC India of suchdifficulty within 30 days after receiving the statement shall serve as an affirmation regarding the acceptance of the statement by me/us. I/We confirm(s) that I/we is/are aware of all security risks involved in receiving the electronically delivered statement. I/We agree(s) that I/we shall not hold HSBC India in any way responsible for the same. I/We agree that the statement will not be generated if no transactions are effected during the stipulated period of the statement cycle. I/We agree that duplicate statements of past transactions are issued against payment of charges as per HSBC India�s current Tariff. In case of receiving e-statements through HSBC Internet Banking, I/We agree to be bound by the terms and conditions mentioned on the website www.hsbc.co.in </p> <p class=para>I agree and acknowledge that the existence of this credit facility and details thereof (including details ofTransactions and any defaults committed by me), will be recorded with credit reference agencies and suchInformation (including processed information) may be shared with banks/financial institutions and other credit grantors for the purposes of assessing further applications for credit by me and/or members of my household, and for occasional debt tracing and fraud prevention. </p> <p class=para>Accordingly, I hereby agree and give consent for the disclosure by HSBC of all or any such; (a) information and data relating to me (b) the information or data relating to any credit facility availed of/to be availed, by me and (c) default, if any, committed by me in discharge of my such obligation, as HSBC may deem appropriate and necessary, to Credit Information Bureau (India) Limited (CIBIL) and any other agency authorized in this behalf by the Reserve Bank of India (RBI). </p> <p class=para>I undertake that (a) CIBIL and any other agency/bureau so authorized may use, process the said information and data disclosed by HSBC; and (b) CIBIL and any other agency so authorized may furnish for consideration, the processed information and data or products thereof prepared by them, to banks/ financial institutions and other credit grantors, as may be specified by the RBI in this behalf </p> <p class=para>I give my/our consent and authorize HSBC to generate subsequently an additional report through CIBIL or Authorized agency/bureau to process my Credit Card application, in the event the necessary documentation and Processes are not completed as per HSBC�s policies.</p> <p class=para>I further authorize HSBC to send and agree to receive SMS alerts/E-mails related to my application status and account activity as well as product use messages that HSBC will send, from time to time, on my mobile phone number/E-mail ID as mentioned in this application form. I undertake to intimate HSBC in the event of any change in my mobile phone number/E-mail ID. I agree that in the event I fail to inform the new mobile number/email id, I will not hold HSBC responsible for non-communication of any information regarding my application status and/or account activity. </p> <p class=para>I further acknowledge that HSBC shall have the right to seek any information from any other source in this regard. I understand that all of the above mentioned information shall form the basis of any facility that HSBC may decide to grant to me/us at its sole discretion. </p> <p class=para>Issued by The Hong Kong and Shanghai Banking Corporation Limited, India. Incorporated in Hong Kong SAR with limited liability. </p> </div> </div> </uib-accordion> </div> </div> <div class=row ng-if=!$ctrl.isStaffJourney> <div class=\"col-md-12 col-sm-12 col-xs-12\"> <uib-accordion close-others=oneAtATime> <div uib-accordion-group is-open=$ctrl.status[3].open ng-init=\"\" class=panel-default> <uib-accordion-heading> <span class=accordianHeader>Video Customer Identification Process-Terms and Conditions</span> <span style=float:right> <span ng-if=$ctrl.status[3].open class=\"icon icon-chevron-up\"></span> <span ng-if=!$ctrl.status[3].open class=\"icon icon-chevron-down\"></span> </span> </uib-accordion-heading> <div class=videoKycTc> <h5 class=para-heading><b>Terms and Conditions for Video based Customer Identification Process (V-CIP)</b></h5> <p class=para>This document lays out the \"Terms and Conditions\", which shall be applicable to all account opening forms and credit card applications processed using the <b>�Video based Customer Identification Process (V-CIP)</b> service provided by HSBC India. </p> <p class=para>Before usage of this service, customers are required to carefully read and understand these Terms and Conditions. Usage of this service by the Customer shall be construed as deemed acceptance of these Terms and Conditions mentioned herein below. </p> <p class=para>The Customer also be bound by the specific Terms and Conditions relating to any account or any of the products or any other services offered by HSBC India and availed of by the Customer. The Customer thereby agrees to abide by these Terms and Conditions as amended by the Bank from time to time which shall be in addition to and not in derogation of the product specific Terms and Conditions unless otherwise specifically stated. </p> <h5 class=para-heading><b>1. Definitions :</b></h5> <p class=para>In this document, the following words and phrases shall have the meanings as set below unless the context indicates otherwise. </p> <p class=para>a. 'HSBC India' means The HongKong & and Shanghai Banking Corporation Limited, India, a Company incorporated under the Companies Ordinance of the Hong Kong Special Administrative Region (HKSAR), having its Registered� Office at 1, Queens Road Central, Hong Kong and its India corporate office at 52/60 Mahatma Gandhi Road, Fort, Mumbai - 400 001. </p> <p class=para>b. 'Application Form' - means the Application Form submitted by the Customer for availing of banking products offered by HSBC India.</p> <p class=para>c. �Banking Products� means and includes all products offered by the Bank wherein HSBC India has allowed the usage of Video KYC solution for onboarding of customers. </p> <p class=para>d. 'Customer' refers to a prospective customer of HSBC India who has applied for a product offered by HSBC India or an existing customer of HSBC India. </p> <p class=para>e. �UIDAI� means The Unique Identification Authority of India which is a statutory authority established under the provisions of the Aadhaar (Targeted Delivery of Financial and Other Subsidies, Benefits and Services) Act, 2016 as amended by the Aadhaar and Other Laws (Amendment) Act, 2019. </p> <p class=para>f. �Customer information� means Name, Address, Photograph, Date of Birth, Gender, Mobile Number, Email ID, PAN, Last 4 digits of Aadhaar No. and all other details which are submitted by the customer as part of availing the services. </p> <h5 class=para-heading><b>2. Scope of services</b></h5> <p class=para>a. Video Customer Identification Process(VCIP) is a method of customer identification by an official of HSBC India by undertaking seamless, secure, real-time, consent based audio-visual interaction with customer to obtain identification / customer information including documents/details for customer due diligence purpose and to ascertain the veracity of the information furnished by the customer. V-CIP process will be used to a. establish an account based relationship with an individual customer or periodic KYC refresh of the customer as permitted by regulator from time to time. HSBC may use either OTP based Aadhaar e-KYC authentication or Aadhaar Paperless offline e-KYC, take customers photograph, take PAN card photograph, obtain live location of customer through geotagging, perform liveliness checks of customer, use of banking correspondents as permitted etc to establish customer information. HSBC reserves the right to implement and/or amend the V-CIP process as permitted by Reserve Bank of India from time to time. </p> <p class=para>b. Subject to these Terms and Conditions HSBC India shall provide V-CIP services from time to time to enable Customers to be onboarded by HSBC by completion of their KYC formalities through V-CIP. </p> <h5 class=para-heading><b>3. Use of the Services</b></h5> <p class=para>a. To access the Services for the first time, the Customer is required to accept all the terms and conditions governing the use of the services and to provide such Information as HSBC India may reasonably specify for identifying him/her.</p> <p class=para>b. By registering to use the Services, the Customer warrants that all customer information provided by the Customer to HSBC India in relation to the Services is true, complete and up-to-date. HSBC India shall not be required to independently verify the same. </p> <p class=para>c. The Services are for the sole and exclusive use by the Customer. </p> <h5 class=para-heading><b>4. Fees</b></h5> <p class=para>HSBC India reserves the right to charge fees in relation to the use and/or termination of the Services including, without limitation, to impose a charge for its provision of the Security Device and to revise such fees. HSBC India shall determine and give reasonable notice to the Customer of the rate of any fee from time to time before they become effective, which shall be binding on the Customer if the Customer continues to maintain or use the Services on or after the effective date. Fees may be collected from the Customer in such manner and at such intervals as HSBC India may specify. </p> <h5 class=para-heading><b>5. Customer's Undertakings and Responsibilities</b></h5> <p class=para>a. The Customer undertakes to provide the customer information to HSBC India and authorizes HSBC India to collect and store customer�s all demographic details (including email id and mobile number) available through Aadhaar Paperless offline e-KYC data ( Aadhaar XML) uploaded by the customer on the VCIP portal. In line with the RBI regulations, Customer further irrevocably and unconditionally authorizes HSBC India to <br> i. capture his/her live location (Geotagging) to ensure that he/she is physically present in India during the VCIP process. <br> ii. use and retain the Customer Information, for KYC verification for the purpose of Account Opening / Re-KYC process </p> <p class=para>b. The Customer shall provide such Information as HSBC India may from time to time reasonably request for the purposes of providing the Services. The Customer shall at all times ensure that: <br>i. all customer information provided to HSBC India is accurate, complete and up-to-date including, without limitation, the Customer's address and other contact details. <br>ii. the Aadhaar XML uploaded on HSBC Portal is not older than 3 days from the date of download from UIDAI. </p> <p class=para>c. The Customer understands that all field(s) in the Online Application Form are mandatory (except where specified otherwise) and undertakes that if any field is left blank, or is filled with incorrect or incomplete data, the Online Application Form shall be considered as incomplete and may be rejected by HSBC India. The Customer undertakes, agrees and confirms that any and all information/details/ data/ documents etc provided by him/her (including PAN details / Form 60, nomination details, FATCA declaration etc) shall be accurate, up to date, genuine, true and correct in every manner whatsoever. </p> <p class=para>d. HSBC India will take reasonable care to ensure that customer information which is stored or transmitted using the Services remains confidential and is not disclosed to any third parties outside the HSBC Group without the Customer's written permission. However, the Customer authorizes HSBC India to disclose or share Information relating to him or his accounts(as the case maybe): <br> i. to any agent, contractor or Third Party service provider who provides services to HSBC India in connection with the operation of its business; <br> ii. in respect of the customer's application for Third Party Product (if and when made available, apply for any Third Party Product by submitting the relevant application through the intermediary of HSBC India ), to the relevant Third Party provider to the extent necessary for the purpose thereof; and <br> iii. where HSBC India is obliged to comply with the orders of courts, government agencies or other lawful authorities anywhere in the world or where it reasonably thinks it necessary in order to give effect to any Instruction or generally to enable HSBC India to provide the services. The Customer and HSBC India shall comply with all applicable data protection laws. <br> iv. The Customer confirms that all persons whose personal or other data is transmitted, processed or otherwise handled, have consented to such transmission, processing or other handling under these Terms in accordance with these laws, or will do so prior to any such transmission, processing or other handling. The Customer further agrees to indemnify and hold HSBC India harmless from all costs, penalties, damages and other losses incurred as the result of any breach of this provision. </p> <p class=para>e. The Customer shall not, and shall not attempt to decompile, reverse-engineer, translate, convert, adapt, alter, modify, enhance, add to, delete or in any way tamper with, or gain access to, any part of the Services or any Internet site or any software comprised in them. </p> <p class=para>f. The Services or any marketing/promotional messages displayed as part of the Services should not be regarded as an offer or solicitation to sell investments or make deposits or solicitation to subscribe to any other product or service, to any person residing in a jurisdiction outside India, where it is unlawful to make such an invitation or solicitation. </p> <p class=para>g. The Customer shall indemnify and hold HSBC India harmless from and against any/all consequences arising from the Customer not complying with the provisions of any applicable laws, rules or regulations. </p> <h5 class=para-heading><b>6. Confidentiality obligations</b></h5> <p class=para>a. Unless expressly permitted by these Terms, the Customer shall not attempt to : <br>i. sell, transfer, disclose, assign, convey, sub-license, share, loan, distribute, transmit, broadcast, cablecast, put in circulation, download, reproduce, duplicate or otherwise provide or disseminate any confidential Information in any form or i. by any means to any other person or commercially exploit any Confidential Information; <br>ii. remove, obliterate, erase, relocate or modify in any way any proprietary marking on or appearing with the Confidential Information including, without limitation, any trademark or copyright notice; or <br>iii. incorporate or combine the Confidential Information with any other programs. </p> <p class=para>b. The restrictions on disclosure shall not apply to any Confidential Information : - <br>i. Where its disclosure is required under law and regulation by law but only to the extent required by law and only after written notice of the requirement to disclose has been given by the Customer to HSBC India ; or <br>ii. Where HSBC India has expressly agreed in writing to its disclosure. </p> <p class=para>c. The Customer agrees that all right, title and interest in and relating to the Confidential Information and any and all related copyright, patent, trademark, service mark, proprietary property, trade secrets and exclusive works are and shall remain the exclusive property of HSBC India and the respective Information Providers. No right, title or interest other than the right to access the Information and the Reports subject to these Terms and Condition is conveyed or transferred to the Customer. The Customer shall not make any representation or do any act which may be taken to indicate that the Customer has any such right, title or interest. </p> <h5 class=para-heading><b>7. Liabilities of HSBC India</b></h5> <p class=para>a. HSBC India will take reasonably practicable steps to ensure that its systems in connection with the services are installed with adequate security designs and to control and manage the risks in operating the systems, taking into account any law, rules, regulations, guidelines, circulars, codes of conduct and prevailing market practices which may be applicable to it from time to time. </p> <p class=para>b. Neither HSBC India, nor any member of the HSBC Group nor any Information Provider warrants or represents that the Services, the Information and the Reports are free from virus or other destructive features which may adversely affect the Customer's hardware, software or equipment. </p> <p class=para>c. HSBC India may suspend any service provided to the Customer under the Services without notice where it considers it necessary or advisable to do so, for example to protect the Customer when there is a suspected breach of security or HSBC India needs to suspend the Services for maintenance or other reasons. Due to the nature of the Services, HSBC India does not warrant that access to the services shall be uninterrupted, timely, or error free. HSBC India will use reasonable efforts to inform the Customer without undue delay through the services, and/or its Website if any of the Services is not available. In the event that HSBC India has levied any charge to the Customer, which is specifically expressed to be for a particular service that is not available (which for the avoidance of doubt shall not mean any periodic fee charged for the Services as a whole) then it shall reimburse the Customer this sum. Other than reimbursing any sum as set out above, HSBC India will have no further liability to the Customer. </p> <p class=para>d. Unless due to the gross negligence or willful default of HSBC India , any member of the HSBC Group or their respective officers or employees, and only to the extent of direct and reasonably foreseeable loss and damage (if any) arising directly and solely there from or the amount of the relevant transaction (whichever is less), neither HSBC India nor any d. member of the HSBC Group assumes any liability or responsibility to the Customer or any other person for the consequences arising from or in connection with : - <br>i. use of the Services and/or access to any Information as a result of such use by the Customer or any other person whether or not authorized; <br>ii. any interruption, interception, suspension, delay, loss, unavailability, mutilation or other failure in providing the Services, in transmitting Instructions or Information relating to the Services or in connecting with the Internet site(s) caused by any acts, omissions or circumstances beyond the reasonable control of HSBC India including, without limitation, failure of any communication network, act or omission of any third party service providers, mechanical failure, power failure, malfunction, breakdown, or inadequacy of equipment, installation or facilities, or any law, rules, regulations, codes, directions, regulatory guidelines or government order (whether or not having the force of law); and <br>iii. transmission and/or storage of any Information and/or data relating to the Customer, the Services and/or transactions or dealings conducted by the Customer pursuant to the Services through or in any system, equipment or instrument of any communication network provider. </p> <p class=para>e. In no event shall HSBC India, any member of the HSBC Group or any Information Provider be liable to the Customer or any other person for any incidental, indirect, special, consequential or exemplary damages including, without limitation, any loss of use, revenue, profits or savings. </p> <h5 class=para-heading><b>8. Liabilities of the Customer</b></h5> <p class=para>a. The Customer shall be fully liable and responsible for all consequences arising from or in connection with use of the Services and/or access to any Information or Report or any other Information as a result of such use by the Customer or any other person whether or not authorized. </p> <p class=para>b. The Customer shall indemnify HSBC India , any member of the HSBC Group, any Information Provider and their respective officers and employees against all liabilities, claims, demand, losses, damages, costs, charges and expenses of any kind (including, without limitation, legal fees on a full indemnity basis) which may be incurred by any of them and all actions or proceedings which may be brought by or against any of them on account of any incorrect information provided by the customer and HSBC/Information Provider acting/relying on the said information. Further, the Customer agrees, at his/her own expense, to indemnify, defend and hold harmless HSBC India and HSBC Group, its directors and employees, representatives, agents, and its Affiliates against any claim, suit, action or other proceeding brought against HSBC India and HSBC Group, its directors and employees, representatives, agents, and Affiliates by a Third P arty, to the extent that such claim, suit, action of other proceeding brought against HSBC India , its directors and employees, representatives, agents, and Affiliates is based on or arises in connection with the usage of services with reference to: <br>i. a violation of the Terms contained herein by the Customer; <br>ii. any unauthorized use of these services; <br>iii. any misrepresentation or breach of representation or warranty made by the Customer contained herein; or <br>iv. any breach of any covenant or obligation to be performed by the Customer hereunder. The Customer agrees to pay any and all costs, damages and expenses, including, but not limited to, reasonable attorneys' fees and costs awarded against it or otherwise incurred by or in connection with or arising from any such claim, suit, action or proceeding attributable to any such claim. </p> <h5 class=para-heading><b>9. Communication</b></h5> <p class=para>a. HSBC India shall be entitled to prescribe, from time to time, the form of notice (whether written or any other form) and the mode of communication with respect to each type of notice to be given pursuant to these Terms and Conditions. HSBC India may also publish notices of general nature, which are applicable to all Customers, on its web site located at www.hsbc.co.in; such notices will have the same effect as a notice served individually to each Customer. </p> <p class=para>b. Communications delivered personally, sent by post, facsimile transmission, telex or email shall be deemed to have been received by the Customer (where delivered personally) at the time of personal delivery or on leaving it at the address last notified in writing by the Customer to HSBC India , (where sent by post) 48 hours after posting if such address is in India and seven days after posting if such address is outside India or (where sent by facsimile transmission, telex or email) immediately after transmitting to the facsimile or telex number or email address last notified in writing by the Customer to HSBC India . Communications sent by the Customer to the Bank shall be treated as delivered to the Bank on the day of actual receipt. </p> <h5 class=para-heading><b>10. Severability</b></h5> <p class=para>Each of the provisions of these Terms is severable and distinct from the others and, if at any time one or more of such provisions is or becomes illegal, invalid or unenforceable in any respect under the laws of any jurisdiction, the legality, validity or enforceability of the remaining provisions shall not be affected in any way. </p> <h5 class=para-heading><b>11. Waiver</b></h5> <p class=para>No act, delay or omission by HSBC India shall affect its rights, powers and remedies under these Terms or any further or other exercise of such rights, powers or remedies. The rights and remedies under these Terms are cumulative and not exclusive of the rights and remedies provided by law. </p> <h5 class=para-heading><b>12. Force Majeure</b></h5> <p class=para>HSBC India shall not be liable for delay in performing or failure to perform any of its obligations under these Terms which is caused by circumstances beyond its reasonable control, including, but not limited to, the failure, malfunction or unavailability of telecommunications, data communications and computer systems and services, war, civil unrest, government action, strikes, lock-outs or other industrial action or trade disputes (whether involving the Banks employees or those of a third party). Any delay or failure of this kind will not be deemed to be a breach of the Terms and Conditions and the time for performance of the affected obligation will be extended by a period which is reasonable in the circumstances. For the purpose of this document 'force majeure' shall be deemed to be any cause affecting the performance of this Agreement arising from or attributable to acts, events, omissions or accidents beyond the reasonable control of the Participant to perform and without limiting the generality thereof shall include the following: <br>i. strikes, lock-outs or other industrial action; <br>ii. civil commotion, riot, invasion, war threat or preparation for war; <br>iii. outbreak of war, rebellion, revolution, hostilities, riot, civil disturbance, acts of terrorism; <br>iv. epidemic, quarantine restrictions, outbreak of debilitating disease and any travel restrictions or bans (including bans on non-essential travel) issued by the World Health Organisation or any governmental authority; <br>v. fire, explosion, storm, flood, earthquake, subsidence, epidemic or other natural physical disaster; <br>vi. impossibility of the use of railways, shipping, aircraft, motor transport or other means of public or private transport; <br>vii. political interference with the normal operations of any Participant; and <br>viii. any change in or introduction of any Laws and Legal Requirements. </p> <h5 class=para-heading><b>13. Miscellaneous </b></h5> <p class=para>a. Where the Account in question is a joint Account, the reference in these Terms to 'Customer' shall be deemed to mean all and each of the joint Account holders. All the Customers shall be bound by these Terms and be jointly and severally liable for all the terms of usage of these services. </p> <p class=para>b. Unless the context otherwise requires, 'person' includes an individual, firm, company, corporation and an unincorporated body of persons </p> <h5 class=para-heading><b>14. Governing Law and Jurisdiction</b></h5> <p class=para>a. The Services and these Terms shall be governed by and construed in accordance with the laws of India. </p> <p class=para>b. HSBC India and the Customer submits to the exclusive jurisdiction of the Courts at Mumbai. </p> <p class=para>c. HSBC India may, however, in its absolute discretion, commence any legal action or proceedings arising out of these terms in any other court, tribunal or other appropriate forum, and the Customer hereby consents to that jurisdiction. Any provision of these terms which is prohibited or unenforceable in any jurisdiction shall, as to such jurisdiction, be ineffective to the extent of prohibition or unenforceability but shall not invalidate the remaining provisions of these terms or affect such provision in any other jurisdiction. </p> </div> </div> </uib-accordion> </div> </div> <div class=row ng-if=!$ctrl.isStaffJourney> <input type=checkbox id=TnCChk name=TnCChk class=screen-reader-only ng-focus=\"$ctrl.hasTnCChkFocus = true;\" ng-blur=\"$ctrl.hasTnCChkFocus = false;\" ng-model=$ctrl.model.TnCChk aria-describedby=nonTnCChkErrMsg aria-invalid=\"{{step8Form.TnCChk.$invalid && (step8Form.TnCChk.$touched || $ctrl.submitted)}}\" required/> <label for=TnCChk id=salariedLbl class=checkbox-label ng-class=\"{'focus': $ctrl.hasTnCChkFocus, 'error': step8Form.TnCChk.$invalid && (step8Form.TnCChk.$touched || $ctrl.submitted)}\"> By Clicking on this Check box, Customer accepts to be bound by the above terms and conditions related to Credit Card Application and Video Customer Identification process </label> <div id=nonTnCChkErrMsg ng-class=\"{'has-error': step8Form.TnCChk.$invalid && (step8Form.TnCChk.$touched || $ctrl.submitted)}\"> <validation-msg ng-if=\"step8Form.TnCChk.$error.required && step8Form.TnCChk.$touched\"> Please check T&C Check box if you want to proceed. </validation-msg> </div> </div> <step-footer ng-if=$ctrl.isStaffJourney show-back=true back-callback=$ctrl.goback() show-vcip=$ctrl.showPWEButton vcip-button-id=vcip-btn vcip-callback=\"$ctrl.initiate('continue-btn','wvcip-btn')\" vcip-overlay=true show-verify=\"!$ctrl.showPWEButton && !$ctrl.isEsignCallbackReceived\" verify-disabled=!true verify-callback=$ctrl.verifyLEE() show-retrigger=\"!$ctrl.showPWEButton && !$ctrl.isEsignCallbackReceived\" retrigger-disabled=$ctrl.retriggerDisablebutton retrigger-callback=$ctrl.reTriggerinitiate() show-continue=\"!$ctrl.showPWEButton && !$ctrl.hidecontinousButton || $ctrl.isEsignCallbackReceived\" continue-button-id=continue-btn continue-callback=$ctrl.next(true)> </step-footer> <step-footer ng-if=!$ctrl.isStaffJourney show-back=true back-callback=$ctrl.goback() show-continue=true continue-button-id=continue-btn continue-disabled=false continue-callback=$ctrl.downloadSignzyDocument(step8Form.$valid,$ctrl.ekycflag)> </step-footer> <div style=padding:10px;display:flex;justify-content:flex-end ng-if=\"$ctrl.retriggerDisablebutton \"> <div style=justify-content:end;display:flex> ({{$ctrl.counter}} sec) </div> </div> </form> </div> </div> <loading ng-show=$ctrl.showLoader></loading>";

